, () => {\n        if (this.images[currentIndex].default === 1) {\n          this.images[0].default = 1\n        }\n        this.images.splice(currentIndex, 1)\n        this.currentIndexImage = 0\n        if (this.images.length) {\n          this.images[0].highlight = 1\n        }\n      }, this.images)\n    },\n    openGallery (index) {\n      this.showLightbox = true\n      this.$refs.lightbox.showImage(index)\n    },\n    onOpenedLightBox (value) {\n      if (value) {\n        this.showLightbox = true\n      } else {\n        this.showLightbox = false\n      }\n    },\n    isValidNumberOfImages (amount) {\n      if (amount > this.maxImage) {\n        this.$emit('limit-exceeded', amount)\n        return false\n      } else {\n        return true\n      }\n    }\n  },\n  watch: {\n    dataImages: {\n      handler: function (newVal) {\n        this.images = cloneDeep(newVal)\n      },\n      deep: true\n    }\n  },\n  mounted () {\n    document.body.addEventListener('dragleave', (event) => {\n      event.stopPropagation()\n      event.preventDefault()\n      this.isDragover = false\n    })\n  },\n  created () {\n    this.images = []\n    this.images = cloneDeep(this.dataImages)\n  }\n}\n</script>\n\n<style lang=\"css\" scoped>\n.text-small {\n  font-size: 11px;\n}\n.position-relative {\n  position: relative;\n}\n.position-absolute {\n  position: absolute;\n}\n.text-center {\n  text-align: center;\n}\n.text-primary {\n  color: #2fa3e6;\n}\n.display-flex {\n  display: flex;\n}\n.flex-column {\n  flex-direction: column;\n}\n.flex-wrap {\n  flex-wrap: wrap;\n}\n.justify-content-center {\n  justify-content: center;\n}\n.justify-content-between {\n  justify-content: space-between;\n}\n.justify-content-end {\n  justify-content: flex-end;\n}\n.align-items-center {\n  align-items: center;\n}\n.full-width {\n  width: 100%;\n}\n.full-height {\n  height: 100%;\n}\n.cursor-pointer {\n  cursor: pointer;\n}\n.centered {\n  left: 50%;\n  transform: translate(-50%, -50%);\n  top: 50