ightbox-thumbnail-arrow vue-lightbox-thumbnail-right\"><span><svg fill=\"white\" x=\"0px\" y=\"0px\" width=\"100%\" height=\"100%\" viewBox=\"0 0 512 512\"><path d=\"M298.3,256L298.3,256L298.3,256L131.1,81.9c-4.2-4.3-4.1-11.4,0.2-15.8l29.9-30.6c4.3-4.4,11.3-4.5,15.5-0.2l204.2,212.7 c2.2,2.2,3.2,5.2,3,8.1c0.1,3-0.9,5.9-3,8.1L176.7,476.8c-4.2,4.3-11.2,4.2-15.5-0.2L131.3,446c-4.3-4.4-4.4-11.5-0.2-15.8 L298.3,256z\"></path></svg></span></button>\n          </div>\n        </div>\n\n        <button type=\"button\" title=\"Previous\" class=\"swiper-button-prev vue-lightbox-arrow vue-lightbox-left\">\n          <span>\n            <svg fill=\"white\" x=\"0px\" y=\"0px\" width=\"100%\" height=\"100%\" viewBox=\"0 0 512 512\"><path d=\"M213.7,256L213.7,256L213.7,256L380.9,81.9c4.2-4.3,4.1-11.4-0.2-15.8l-29.9-30.6c-4.3-4.4-11.3-4.5-15.5-0.2L131.1,247.9 c-2.2,2.2-3.2,5.2-3,8.1c-0.1,3,0.9,5.9,3,8.1l204.2,212.7c4.2,4.3,11.2,4.2,15.5-0.2l29.9-30.6c4.3-4.4,4.4-11.5,0.2-15.8 L213.7,256z\"></path></svg>\n          </span>\n        </button>\n        <button type=\"button\" title=\"Next\" class=\"swiper-button-next vue-lightbox-arrow vue-lightbox-right\">\n          <span>\n            <svg fill=\"white\" x=\"0px\" y=\"0px\" width=\"100%\" height=\"100%\" viewBox=\"0 0 512 512\" xml:space=\"preserve\"><path d=\"M298.3,256L298.3,256L298.3,256L131.1,81.9c-4.2-4.3-4.1-11.4,0.2-15.8l29.9-30.6c4.3-4.4,11.3-4.5,15.5-0.2l204.2,212.7 c2.2,2.2,3.2,5.2,3,8.1c0.1,3-0.9,5.9-3,8.1L176.7,476.8c-4.2,4.3-11.2,4.2-15.5-0.2L131.3,446c-4.3-4.4-4.4-11.5-0.2-15.8 L298.3,256z\"></path></svg>\n          </span>\n        </button>\n      </div>\n    </div>\n  </transition>\n</template>\n\n<script>\nimport 'swiper/dist/css/swiper.css'\nimport { swiper, swiperSlide } from 'vue-awesome-swiper'\nexport default {\n\n  name: 'Modal',\n\n  props: {\n    show: {\n      type: Boolean,\n      default: false\n    },\n    showThumbs: {\n      type: Boolean,\n      default: true\n    },\n    showCaption: {\n      type: Boolean,\n      default: true\n    },\n    images: {\n      type: Array,\n      default: function () {\n        return []\n      }\n    }\n  },\n  data () {\n    return {\n      activeIndex: 0,\n      swiperOptionTop: {\n        centeredSlides: true,\n        slidesPerView: 1,\n        navigation: {\n          nextEl: '.vue-lightbox-right',\n          prevEl: '.vue-lightbox-left'\n        }\n      },\n      swiperOptionThumbs: {\n        spaceBetween: 10,\n        slidesPerView: 4,\n        centeredSlides: true,\n        touchRatio: 0.2,\n        slideToClickedSlide: true,\n        navigation: {\n          nextEl: '.vue-lightbox-thumbnail-right',\n          prevEl: '.vue-lightbox-thumbnail-left'\n        }\n      }\n    }\n  },\n  components: {\n    swiper,\n    swiperSlide\n  },\n  methods: {\n    close () {\n      this.$emit('close')\n    },\n    showImage (index = 0) {\n      this.$nextTick(() => {\n        this.$refs.swiperTop.swiper.slideTo(index)\n        this.$refs.swiperThumbs.swiper.slideTo(index)\n        this.activeIndex = index\n      })\n    }\n  },\n  mounted () {\n    document.addEventListener('keydown', (e) => {\n      if (this.show && e.keyCode == 27) {\n        this.close()\n      }\n    })\n    this.$nextTick(() => {\n      const swiperTop = this.$refs.swiperTop.swiper\n      const swiperThumbs = this.$refs.swiperThumbs.swiper\n      swiperTop.controller.control = swiperThumbs\n      swiperThumbs.controller.control = swiperTop\n      swiperTop.on('slideChange', () => {\n        this.$emit('change', swiperTop.activeIndex)\n        this.activeIndex = swiperTop.activeIndex\n      })\n    })\n  }\n}\n</script>\n\n<style lang=\"css\" scoped>\n* {\n  box-sizing: border-box;\n}\n.modal-mask {\n  color: #fff;\n  background-color: rgba(0, 0, 0, .8);\n  display: flex;\n  position: fixed;\n  padding: 10px;\n  z-index: 9998;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  align-items: center;\n  justify-content: center;\n  box-align: center;\n  box-pack: center;\n  transition: opacity .3s ease;\n  box-sizing: border-box;\n}\n.modal-container {\n\n}\n\n/*\n * The following styles are auto-applied to elements with\n * transition=\"modal\" when their visibility is toggled\n * by Vue.js.\n *\n * You can easily play with the modal transition by editing\n * these styles.\n */\n\n.modal-enter {\n  opacity: 0;\n}\n\n.modal-leave-active {\n  opacity: 0;\n}\n\n.modal-enter .modal-container,\n.modal-leave-active .modal-container {\n  -webkit-transform: scale(1.1);\n  transform: scale(1.1);\n}\n\n.swiper-container {\n  background-color: #000;\n}\n.swiper-slide {\n  background-size: cover;\n  background-position: center;\n}\n.swiper-button-prev.swiper-button-disabled, .swiper-button-next.swiper-button-disabled {\n  opacity: 0;\n}\n\n/*\n * Lightbox\n */\n\n* {\n  box-sizing: border-box;\n}\n.vue-lightbox-content {\n  margin-bottom: 60px;\n  max-width: calc(100vw - 30px);\n  position: relative;\n}\n.vue-lightbox-header {\n  display: flex;\n  height: 40px;\n  justify-content: space-between;\n}\n.vue-lightbox-close {\n  background: none;\n  border: none;\n  cursor: pointer;\n  outline: none;\n  position: relative;\n  top: 0;\n  vertical-align: bottom;\n  height: 40px;\n  margin-right: -10px;\n  padding: 10px;\n  width: 40px;\n}\n.vue-lightbox-figure {\n  margin: 0;\n  display: block;\n  position: relative;\n}\n.vue-lightbox-figure .swiper-slide {\n  margin: auto;\n}\nimg.vue-lightbox-modal-image {\n  max-height: calc(100vh - 140px);\n  cursor: pointer;\n  display: block;\n  height: auto;\n  margin: 0 auto;\n  max-width: 100%;\n  user-select: none;\n}\n.vue-lightbox-info {\n  visibility: initial;\n  position: absolute;\n  bottom: 0;\n  color: #fff;\n  background-color: rgba(0,0,0,.7);\n  height: 40px;\n  width: 100%;\n  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;\n  text-align: center;\n}\n.vue-lightbox-info h4 {\n  font-size: 18px;\n  font-weight: 500;\n  margin-top: 10px;\n  margin-bottom: 10px;\n}\n.vue-lightbox-footer {\n  box-sizing: border-box;\n  color: #fff;\n  cursor: auto;\n  display: flex;\n  left: 0;\n  line-height: 1.3;\n  padding: 5px 0;\n  justify-content: space-between;\n}\n.vue-lightbox-footer-info {\n  display: block;\n  flex: 1 1 0;\n}\n.vue-lightbox-footer-count {\n  color: hsla(0,0%,100%,.75);\n  font-size: .85em;\n  padding-left: 1em;\n}\n\n.vue-lightbox-thumbnail-wrapper {\n  bottom: 10px;\n  height: 50px;\n  left: 0;\n  margin: 0 auto;\n  position: absolute;\n  right: 0;\n  text-align: center;\n  top: auto;\n}\n.vue-lightbox-thumbnail {\n  bottom: 10px;\n  height: 50px;\n  padding: 0 50px;\n  text-align: center;\n  white-space: nowrap;\n  display: inline-block;\n  position: relative;\n  width: 300px;\n}\n\n.vue-lightbox-thumbnail .swiper-container {\n  background: transparent;\n}\n\n.vue-lightbox-thumbnail-arrow {\n  background: none;\n  border: none;\n  border-radius: 4px;\n  cursor: pointer;\n  outline: none;\n  padding: 10px;\n  position: absolute;\n  top: 50%;\n  user-select: none;\n  height: 50px;\n  margin-top: -23px;\n  width: 30px;\n}\n.vue-lightbox-thumbnail-left {\n  left: 10px;\n}\n.vue-lightbox-thumbnail-right {\n  right: 10px;\n}\n.vue-lightbox-modal-thumbnail, .vue-lightbox-modal-thumbnail-active {\n  background-position: 50%;\n  background-size: cover;\n  border-radius: 2px;\n  cursor: pointer;\n  display: inline-block;\n  height: 50px;\n  margin: 2px;\n  overflow: hidden;\n  width: 50px;\n  float: left;\n}\n.vue-lightbox-modal-thumbnail {\n  box-shadow: inset 0 0 0 1px hsla(0,0%,100%,.2);\n}\n.swiper-slide-active .vue-lightbox-modal-thumbnail {\n  box-shadow: inset 0 0 0 2px #fff;\n}\n\n\n.vue-lightbox-arrow {\n  background: none;\n  border: none;\n  border-radius: 4px;\n  cursor: pointer;\n  outline: none;\n  padding: 10px;\n  position: absolute;\n  top: 50%;\n  height: 120px;\n  margin-top: -60px;\n  user-select: none;\n  width: 40px;\n}\n.vue-lightbox-left {\n  left: 10px;\n}\n.vue-lightbox-right {\n  right: 10px;\n}\n\n@media (min-width: 1200px) {\n  .vue-lightbox-content {\n    max-width: 1024px;\n  }\n}\n@media (min-width: 768px) {\n  .vue-lightbox-arrow {\n    width: 70px;\n  }\n}\n@media (min-width: 500px) {\n  .vue-lightbox-thumbnail-arrow {\n    width: 40px;\n  }\n}\n</style>\n"],"sourceRoot":""}]);
